/* game-filter.css */
.filter-container {
    margin-bottom: 10px;
    position: absolute;
    z-index: 2;
}

.filter-button {
    background-color: #000000;
    color: rgb(255, 255, 255);
    padding: 8px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 5px;
}

.filter-button:hover {
    background-color: #3c3c3d;
}

.checkbox-container {
    margin-top: 10px;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.checkbox-container label {
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
    color:black
}

.checkbox-container input {
    margin-right: 10px;
}
